
.katex * {
    border-color: currentColor !important;
}

#Actividad_Warp {
    height: 100%;
}
#Actividad_Main{
    z-index: 1;
}

#Actividad_Main .pantalla {
    display: block;
    position: relative;
    font-family: 'Muli', sans-serif;
    font-size: 24px;
    color: black;
    width: 100%;
}

.wrapper-contenido:first-child div {
    min-height: 0;
}

.btn-maco {
    font-size: 18px;
    font-weight: 700;
    color: #fff;
    padding: 13px 22px;
    background: #FF5601;
    border-radius: 30px;
    text-transform: uppercase;
}

.btn:hover, .btn:focus {
    color: #333;
    text-decoration: none;
}

a.btn-maco:visited, a:link, .btn.btn-maco:hover, .btn.btn-maco:focus {
    color: #fff;
}
